import React from "react"
import { Link, graphql } from "gatsby"
import Layout from "../components/layout"

export default ({ data }) => (
  <Layout>
    <h1>About {data.site.siteMetadata.title}</h1>
    <p>This is the personal site of Jeska Duman. I am a freelance web developer based in Chicago.</p>
    <p>
      I started building websites as a hobby as a kid and have generally built web projects to support my other interests. I've been
      been doing professional web development since 2016. Currently I make digital products for small and mid-sized brands and nonprofits on a freelance basis - 
      see <a href="https://www.jeskaduman.com">jeskaduman.com</a> for more info.
    </p>
    <p>Most of my projects are built with Wordpress.</p>
    <p>My main hobbies are creative writing and <a href="https://www.instagram.com/jeskaduman">DIY fashion</a>. I consider myself a 
      lifelong autodidact, although I was a first-generation college graduate and received a bachelor's in history 
      from Marymount Manhattan College. I am originally from Michigan, and grew up between St. Clair County and Petoskey.
    </p>
    <h2>About this site</h2>
    <p>This site was built in <a href="https://www.reactjs.com">React</a> using <a href="https://www.gatsbyjs.org">Gatsby</a>. 
    It uses <a href="https://www.styled-components.com/">Styled Components</a> and Sass for styling.</p>
    <Link to="/">Back to Home</Link>
  </Layout>
)

export const query = graphql`
  query {
    site {
      siteMetadata {
        title
      }
    }
  }
`